import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {DEVICE_STATUS} from './constants/device';
import {Device} from './device-manager/device-manager.component';

@Injectable()
export class DeviceService {
  private deviceSource = new BehaviorSubject<Device>({
    deviceId: null,
    port: null,
    canvasWidth: 0,
    canvasHeight: 0,
    status: DEVICE_STATUS.NO_DEVICE,
  });

  currentDeviceInfo = this.deviceSource.asObservable();

  constructor() {}

  updateDevice(device: Device) {
    this.deviceSource.next(device);
  }
}



